.settingss-div {
    height: 97vh;
    background-color: #e2eef7;
    color: #364d5d;
    width: 100%;
    padding: 10px;
  }


.settingss-div.darkmode{
  background-color: #202a3f;
  color: white;
}

  .backsymbol{
  font-weight: bold;
  padding-right: 10px;
  }

  .back{
    float: left;
    margin-left: 7px;
  }


  .single-div{
    background-color: #ebf4fb;
    color: #364d5d;
    width: 90%;
    height: 6vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    padding-left: 10%
  }

  .single-div.darkmode{
    background-color: #202a3f;
    color: white ;
  }

  .single-div:hover{
    background-color: #364d5d;
    color:white;
  }

  .setting-profile{
    width: 70px;
    height: 70px;
    border-radius: 50%;
  }

  .set-pro{
    
    display: flex;
    align-items: center;
    padding-left: 20px;
    height: 10vh;
    gap: 20px;
    font-weight: bolder;

  }

  .set-pro.darkmode{

    color: white;
  }

  .set-icon{
    padding-right: 20px;
  }

  .set-status{
   font-size: small;
  }