.user-login {
    color: #3f4447;

}

.input-items {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}

.input-items .input {
    margin: 10px 0;
    padding: 10px;
    border: none;
    outline: none;
    border-radius: 10px;
    width: 60%;
    box-sizing: border-box;
}

.input-items .error {
    color: red;
    margin-top: 10px;
}

.input-items a {
    color: blue;
    text-decoration: none;
    margin-top: 10px;
}

.input-items Button {
    margin-top: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 10px;
    padding: 10px 20px;
    cursor: pointer;
}

.input-items Button .anticon {
    margin-right: 5px;
}

.icons {
    display: flex;
    justify-content: center;
    /* margin-top: 20px; */
    /* width: 150px; */
    padding: 15px;
}

.icons svg {
    width: 50px;
    height: 50px;
    fill: #fff;
    margin: 0 10px;
    cursor: pointer;
}


.centered-loading-text {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease-in-out;
}

.centered-loading-text:hover{
    border: 1px solid #fff;
    transform: scale(1.1);
}

.input:focus{
    border: 1px solid black;
}

.input {
    text-align: center; 
}

.input::placeholder {
    text-align: center; 
}

.input {
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.input{
    
    border: 1px solid #ccc;
    padding: 10px;
    transition: border 0.3s ease; 
}


.input:hover {
    border: 1px solid #007bff; 
    
}