.groups-div {
    height: 97vh;
    background-color: #e2eef7;
    color: #364d5d;
    width: 100%;
    padding: 10px;
}

.groups-div.darkmode {
    background-color: #181f2e;
    color: white;
}

.group-btn {
    height: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.group-btn.darkmode {
    background-color: #181f2e;
    color: white;
}

.grp-btn {
    width: 60%;
    height: 100%;
    border: none;
    background: #364d5d;
    color: white;
    border-radius: 20px;
    transition: 0.3s;
}

.grp-btn:hover {
    background: #202a3f;
    border: white;
    outline: white;
}

