
.forget-password-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f4f4f4;
  }
  
  .forget-pass-container {
    display: flex;
    align-items: center;
    
  }
  
  .forpass-img {
    max-width: 40%;
    height: auto;
    margin-right: 20px; 
  }
  
  .form-container {
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 2px 4px rgba(0, 0, 1, 1);
    padding: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    max-width: 400px; 
    width: 100%;
    height: 100%;
    min-height: 400px;
    
  }
  
  .pass-txt {
    padding-top: 15px;
    font-size: 12px;
    margin-bottom: 10px;
    color: red;
  }
  
  .form-label {
    font-size: 18px;
    margin-bottom: 10px;
    display: block;
  }
  
  .form-input {
    width: 80%;
    padding: 10px;
    padding-left: 55px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    font-size: 16px;
    display: flex;
    text-align: left;
    
  }
  
  .form-button {
    background-color: #1890ff;
    color: #fff;
    border: none;
    border-radius: 5px;
    width: 96%;
    font-size: 18px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .form-button:hover {
    background-color: #0050b3;
  }

  .form-int{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  

  .back-login{
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    cursor: pointer;
    
  }

  .form-label{
    font-family: monospace;
  }